import { $axios } from "./app-http";

export async function registerPatient(payload) {
    const { data } = await $axios.post(
        "patient-msg-log/register-patient",
        payload
    );
    return data;
}

export async function getIsPatientRegistered(id) {
    const { data } = await $axios.get(
        `patient-msg-log/is-patient-registered/${id}`
    );
    return data;
}

export async function getPatientInformation(id) {
    const {
        data: { patientDetails }
    } = await $axios.get(`patient-msg-log/patient-data/${id}`);
    return patientDetails;
}

export async function searchAllPatient(term) {
    const { data } = await $axios.get("demographics/search-all", {
        params: {
            term
        }
    });
    return data;
}

export async function getPatient(id) {
    const { data } = await $axios.get(`demographics/${id}`);
    return data;
}

export async function getAllMessages() {
    const { data } = await $axios.get("patient-msg-log/all-messages");
    return data;
}

export async function getPatientMessage(id, name) {
    const { data } = await $axios.get("patient-msg-log/patient-messages", {
        params: {
            patientId: id,
            name: name
        }
    });
    return data;
}

export async function composeMessage(payload) {
    const { data } = await $axios.post("patient-msg-log/compose-sms", payload);
    return data;
}

export async function getRelatedMessges(id) {
    const { data } = await $axios.get("patient-msg-log/related-msg", {
        params: { parentMsgId: id }
    });
    return data;
}
